import { Event } from "@front/util_function/eventBus";
import { io, Socket } from "socket.io-client";
import debounce from "lodash/debounce";
class WsEvent extends Event {
	registered = false;
	io: Socket | undefined;
	id = "";
	constructor() {
		super();
		this.id = "";
	}

	register(id: string) {
		this.id = id;
		if (!this.checkRegister()) {
			this.io = io(`http://${window.location.hostname}:3000`, {
				query: {
					id
				}
			});
			this.io.on("connect", () => {
				this.registered = true;
				this.id = id;
				this.emit("registered");
			});
			this.io.on("transmit", (data: any) => {
				const realData = JSON.parse(data);
				this.emit(realData.event, realData.data);
			});
			this.io.on("disconnect", (e: any) => {
				this.registered = false;
				this.io = undefined;
				this.reRegister();
			});
		}
		return Promise.resolve();
	}

	reRegister = debounce(() => {
		if (this.id) {
			return this.register(this.id);
		} else {
			console.log("not registered yet!");
			return false;
		}
	}, 1500);

	checkRegister() {
		return this.io?.connected;
	}

	wsEmit(event: any, data: any = {}, to = "") {
		if (!this.io) return;
		this.io.emit(
			"transmit",
			JSON.stringify({
				event,
				from: this.id,
				to,
				data
			})
		);
	}
}

export const wsevent = new WsEvent();
